import logo from './logo.svg';
import './App.css';

// Get redux in app
import {Provider} from 'react-redux'
import store from './store'

import {BrowserRouter as Router, Route} from 'react-router-dom';

import Compose from "./components/Compose.js"
import PostBox from "./components/PostBox.js"
import Post from "./components/Post.js"
import NavBar from "./components/NavBar.js"
import Footer from "./components/Footer.js"

import Layout from "./hoc/Layout.js"
import Home from "./containers/Home.js"
import Login from "./containers/Login.js"
import Register from "./containers/Register.js"

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCoffee, faSyncAlt } from '@fortawesome/free-solid-svg-icons'

import React, { useState } from 'react';
import { useEffect } from 'react';

function App() {
  const [latestPosts, setLatestPosts] = useState([]);
  const [loaded, setLoaded] = useState(false);

  useEffect(() => {
    const getPosts = async () => {
      const tasksFromServer = await fetchPosts()
      setLatestPosts(tasksFromServer)
      setLoaded(true);
    }
  
    getPosts()
  }, [])

  // get Posts
  const fetchPosts = async () => {
    const res = await fetch('http://127.0.0.1:8000/network/posts/1')
    const data = await res.json()

    // console.log(data)
    return data
  }

  return (
    <Provider store={store}>
      <div className="App">
        <Router>
          <Layout>
            <Route exact path='/' component={Home}/>
            <Route exact path='/register' component={Register}/>
            <Route exact path='/login' component={Login}/>
          </Layout>
        </Router>
          {(loaded == true) ?
          <PostBox posts={latestPosts}></PostBox> : <p>loading <FontAwesomeIcon className="load_spin" icon={faSyncAlt}></FontAwesomeIcon></p>}
        <Footer></Footer>
        {/* <button onClick={Login}></button> */}
      </div>
    </Provider>
  );
}

export default App;
