import Post from "./Post.js"
import Compose from "./Compose.js"

function PostBox({posts}) {
    const loadPosts = posts.posts

    const postList = loadPosts.map((post) => {
        return <Post username={post.creator} body={post.content} likes={post.likes}></Post>
    })

    console.log(loadPosts[1])

    return (
    <div className="postBox_container">
        <Compose></Compose>
        <div className="break_out"></div>
        {postList}
    </div>
    )

}
export default PostBox